#root {
  min-height: 100vh;
  text-align: center;
  background-image: url(starrynight.jpg);
  background-size: cover;
}

.card {
  text-align: center;
  margin: 4% 30% 4% 30%;
  opacity: 0.8;
}

.card:hover {
  opacity: 1;
}

.card-content {
  text-align: center;
}


.date-text {
  padding: 1% 0 10% 0;
}

.btn {
  margin: 10% 0 0 0;
}

.logo {
  margin-right: 5%;
}

.card-body {
  padding: 2% 10% 2% 10%;
}

.footer > p, .footer-link{
  color: #fff;
}

.social-icon {
  border-radius: 50%;
  background-color: #fff;
}

.social-icon:hover {
  color: #fff;
  transition: all 0.2s ease-in-out;
  transform: scale(1.03);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
@media screen and (max-width: 600px) {
  #quotes {
    padding-bottom: 30%;
  }
  .date-text {
    padding-bottom: 30%;
  }
  .card {
    margin: 4% 15% 4% 15%;
  }
}
